'use client';

import { motion } from "framer-motion";
import { Mail, MessageSquare, Phone, Send, Sparkles } from "lucide-react";
import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";

export function Contact() {
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [formData, setFormData] = useState({
        name: "",
        email: "",
        subject: "",
        message: "",
    });

    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        const { name, value } = e.target;
        setFormData((prev) => ({ ...prev, [name]: value }));
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsSubmitting(true);

        try {
            const response = await fetch('http://localhost:3001/contact', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(formData),
            });

            if (response.ok) {
                setFormData({ name: "", email: "", subject: "", message: "" });
                alert("Message sent successfully!");
            } else {
                alert("Failed to send message. Please try again later.");
            }
        } catch (error) {
            console.error("Error submitting form:", error);
            alert("An error occurred. Please try again later.");
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <section id="contact" className="py-24 relative overflow-hidden">
            {/* Subtle Glow Background */}
            <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[600px] h-[600px] bg-blue-500/10 rounded-full blur-[120px] -z-10" />

            <div className="container mx-auto px-4 md:px-6">
                <div className="text-center mb-16">
                    <motion.div
                        initial={{ opacity: 0, scale: 0.9 }}
                        whileInView={{ opacity: 1, scale: 1 }}
                        className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-primary/10 border border-primary/20 text-primary text-xs font-bold uppercase tracking-widest mb-4"
                    >
                        <Sparkles className="w-3 h-3" />
                        <span>Contact</span>
                    </motion.div>
                    <h2 className="text-3xl md:text-5xl font-bold tracking-tight mb-4">Get in <span className="text-gradient">Touch</span></h2>
                    <p className="text-muted-foreground text-lg max-w-[600px] mx-auto font-light">
                        Ready to start your next project? Drop a message and let's build something extraordinary together.
                    </p>
                </div>

                <div className="grid lg:grid-cols-5 gap-8 max-w-6xl mx-auto">
                    {/* Contact Info Bento */}
                    <motion.div
                        initial={{ opacity: 0, x: -20 }}
                        whileInView={{ opacity: 1, x: 0 }}
                        viewport={{ once: true }}
                        className="lg:col-span-2 space-y-6"
                    >
                        <div className="p-8 glass-morphism h-full flex flex-col justify-between">
                            <div>
                                <h3 className="text-2xl font-bold text-white mb-2">Contact Info</h3>
                                <p className="text-muted-foreground text-sm mb-8 italic">Available for remote work worldwide.</p>

                                <div className="space-y-6">
                                    <div className="flex items-center gap-4 group">
                                        <div className="p-4 rounded-2xl bg-white/5 border border-white/10 text-blue-400 group-hover:bg-blue-500 group-hover:text-white transition-all duration-300">
                                            <Mail className="w-5 h-5" />
                                        </div>
                                        <div>
                                            <div className="text-xs text-muted-foreground uppercase tracking-widest font-bold mb-1">Email Me</div>
                                            <a href="mailto:hello@example.com" className="text-white font-medium hover:text-blue-400 transition-colors">hello@example.com</a>
                                        </div>
                                    </div>

                                    <div className="flex items-center gap-4 group">
                                        <div className="p-4 rounded-2xl bg-white/5 border border-white/10 text-purple-400 group-hover:bg-purple-500 group-hover:text-white transition-all duration-300">
                                            <Phone className="w-5 h-5" />
                                        </div>
                                        <div>
                                            <div className="text-xs text-muted-foreground uppercase tracking-widest font-bold mb-1">Call Me</div>
                                            <a href="tel:+1234567890" className="text-white font-medium hover:text-purple-400 transition-colors">+1 (234) 567-890</a>
                                        </div>
                                    </div>

                                    <div className="flex items-center gap-4 group">
                                        <div className="p-4 rounded-2xl bg-white/5 border border-white/10 text-emerald-400 group-hover:bg-emerald-500 group-hover:text-white transition-all duration-300">
                                            <MessageSquare className="w-5 h-5" />
                                        </div>
                                        <div>
                                            <div className="text-xs text-muted-foreground uppercase tracking-widest font-bold mb-1">Socials</div>
                                            <div className="flex gap-3 mt-1">
                                                {['LinkedIn', 'GitHub', 'Twitter'].map(social => (
                                                    <a key={social} href="#" className="text-sm text-white/60 hover:text-white underline underline-offset-4 decoration-white/20">{social}</a>
                                                ))}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="mt-12 pt-8 border-t border-white/5">
                                <div className="text-white font-bold mb-2 italic">Current Location</div>
                                <div className="text-muted-foreground text-sm flex items-center gap-2">
                                    <span className="w-2 h-2 rounded-full bg-emerald-500 animate-pulse" />
                                    New York, GMT-4
                                </div>
                            </div>
                        </div>
                    </motion.div>

                    {/* Contact Form Bento */}
                    <motion.div
                        initial={{ opacity: 0, x: 20 }}
                        whileInView={{ opacity: 1, x: 0 }}
                        viewport={{ once: true }}
                        className="lg:col-span-3"
                    >
                        <div className="p-8 glass-morphism">
                            <form onSubmit={handleSubmit} className="space-y-6">
                                <div className="grid md:grid-cols-2 gap-6">
                                    <div className="space-y-2">
                                        <label className="text-xs font-bold uppercase tracking-widest text-white/70 ml-1">Full Name</label>
                                        <Input
                                            name="name"
                                            placeholder="John Doe"
                                            value={formData.name}
                                            onChange={handleChange}
                                            required
                                            className="bg-white/5 border-white/10 h-12 focus:border-blue-500/50 focus:ring-blue-500/20 rounded-xl"
                                        />
                                    </div>
                                    <div className="space-y-2">
                                        <label className="text-xs font-bold uppercase tracking-widest text-white/70 ml-1">Email Address</label>
                                        <Input
                                            name="email"
                                            type="email"
                                            placeholder="john@example.com"
                                            value={formData.email}
                                            onChange={handleChange}
                                            required
                                            className="bg-white/5 border-white/10 h-12 focus:border-blue-500/50 focus:ring-blue-500/20 rounded-xl"
                                        />
                                    </div>
                                </div>
                                <div className="space-y-2">
                                    <label className="text-xs font-bold uppercase tracking-widest text-white/70 ml-1">Subject</label>
                                    <Input
                                        name="subject"
                                        placeholder="Project Inquiry"
                                        value={formData.subject}
                                        onChange={handleChange}
                                        required
                                        className="bg-white/5 border-white/10 h-12 focus:border-blue-500/50 focus:ring-blue-500/20 rounded-xl"
                                    />
                                </div>
                                <div className="space-y-2">
                                    <label className="text-xs font-bold uppercase tracking-widest text-white/70 ml-1">Your Message</label>
                                    <Textarea
                                        name="message"
                                        placeholder="Tell me about your project..."
                                        className="min-h-[150px] bg-white/5 border-white/10 focus:border-blue-500/50 focus:ring-blue-500/20 rounded-xl resize-none"
                                        value={formData.message}
                                        onChange={handleChange}
                                        required
                                    />
                                </div>
                                <Button
                                    type="submit"
                                    disabled={isSubmitting}
                                    className="w-full h-14 bg-primary hover:bg-primary/90 text-primary-foreground font-bold rounded-xl shadow-[0_10px_30px_rgba(56,189,248,0.2)] group transition-all"
                                >
                                    {isSubmitting ? (
                                        "Transmitting..."
                                    ) : (
                                        <span className="flex items-center gap-2">
                                            Send Message
                                            <Send className="w-4 h-4 group-hover:translate-x-1 group-hover:-translate-y-1 transition-transform" />
                                        </span>
                                    )}
                                </Button>
                            </form>
                        </div>
                    </motion.div>
                </div>
            </div>
        </section>
    );
}
